<template>
  <div class='dexview'>

    <Pokemon :pokemon='getSelectedPokemon'></Pokemon>

    <section class="dexview__list">

      <div class="dexview__slider">
        <div
          class="dexview__slider__back"
          @click="go(-1)"
        ></div>
        <div class="dexview__slider__box">box {{multiplier}}</div>
        <div
          class="dexview__slider__go"
          @click="go(1)"
        ></div>
      </div>

      <section class="dexview__entry__search">
        <input
          type="text"
          @keyup="search_for"
        >
      </section>

      <!--DEFAULT LIST-->
      <div
        v-if="!searched"
        class="dexview__items"
      >
        <div
          v-for="(val, k) in battledex.slice((start*multiplier)-108, (end*multiplier))"
          :key="k"
          :class="'pokemon-'+ val.types[0] +' pokemon-'+ val.types[0] +'-border'"
          class="dexview__item"
          @click="choosePokemon(val.slug)"
        >
          <div>
            <img
              v-show="val.url"
              :src="'http://bulbadot.herokuapp.com/2d/'+val.slug+'.png'"
              @load="val.url=true"
            >
          </div>
        </div>
      </div>

      <!--SEARCH RESULT LIST-->
      <div
        v-else
        class="dexview__items"
      >
        <div
          v-for="(val, k) in searched.slice(0,108)"
          :key="k"
          :class="'pokemon-'+ battledex[val].types[0] +' pokemon-'+ battledex[val].types[0] +'-border'"
          class="dexview__item"
          @click="dispatchSearch(val)"
        >
          <div>
            <img :src="'http://bulbadot.herokuapp.com/2d/'+val+'.png'">
          </div>
        </div>
      </div>
    </section>

  </div>
</template>

<script>
import Axios from "axios";
import Pokemon from "@/components/Pokemon/_Pokemon.vue";

export default {
  components:{
    Pokemon
  },
  data() {
    return {
      start: 108,
      end: 108,
      multiplier: 1,
      searched: false
    };
  },
  methods: {
    go(value) {
      value = this.multiplier + value;
      if (value <= 0) {
        return;
      }
      if (value > Math.ceil(this.battledex_size / 108)) {
        return;
      }
      this.multiplier = value;
    },
    search_for(ev) {
      const word = ev.target.value.toLowerCase();
      let s = [];
      for (let pkmn in this.battledex) {
        if (word.length > pkmn.length) continue;
        else if (pkmn.startsWith(word)) {
          if (pkmn !== "top") s.push(pkmn);
        }
      }
      this.searched = s;
    },
    dispatchSearch(pokemon) {
      this.setSelectedPokemon(pokemon);
      this.searched = false;
    },
    imgLoad(status) {
      console.log(status);
    },
    choosePokemon(pokemon) {
      Axios.post("/pokemon", { pokemon: pokemon, tier: this.$route.params.tier }).then(res => {
        this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon);
      });
    }
  },
  created() {
    this.$store.commit("dex/SET_LOADING_STATE", false);
    this.choosePokemon(this.battledex[0].slug)
  }
};
</script>